<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right  mt-4">
            <button class="btn btn-primary" @click="openModal(true)">建立新的優惠券</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th>名稱</th>
                    <th width="170px">折扣百分比</th>
                    <th width="150px">到期日</th>
                    <th width="150px">是否啟用</th>
                    <th width="120px">編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,key) in coupons" :key="key">
                    <td>{{ item.title }}</td>
                    <td>{{ item.percent }}%</td>
                    <td>{{ item.due_date | date }}</td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>尚未啟用</span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
                        <button class="btn btn-outline-danger btn-sm" @click="openDelCouponModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>
        <Pagination :pagination="pagination" @pageChange="getCoupons"></Pagination>
        <!-- Modal -->
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span v-if="isNew">新增優惠券</span>
                            <span v-else>修改優惠券</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="form-group">
                            <label for="title">標題</label>
                            <input type="text" class="form-control" id="title" 
                                v-model="tempCoupon.title"
                                placeholder="請輸入標題">
                        </div>
                        <div class="form-group">
                            <label for="title">優惠碼</label>
                            <input type="text" class="form-control" id="title" 
                                v-model="tempCoupon.code"
                                placeholder="請輸入優惠碼">
                        </div>
                        <div class="form-group">
                            <label for="title">到期日</label>
                            <input type="date" class="form-control" id="title" 
                                v-model="due_date"
                                placeholder="請輸入到期日">
                        </div>
                        <div class="form-group">
                            <label for="title">折扣百分比</label>
                            <input type="text" class="form-control" id="title" 
                                v-model="tempCoupon.percent"
                                placeholder="請輸入折扣百分比">
                        </div>
                        <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" id="is_enabled" 
                                    v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>刪除優惠券</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        是否刪除 <strong class="text-danger">{{ tempCoupon.title }}</strong> 商品(刪除後將無法恢復)。
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-danger"
                        @click="deleteCoupon"
                        >確認刪除</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
import Pagination from './Pagination'
    export default{
        data() {
            return{
                coupons:[],
                tempCoupon:{},
                isNew:false,
                isLoading:false,
                pagination:{},
                due_date:""
            }
        },
        methods:{
            getCoupons(page = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`
                const vm = this
                vm.isLoading = true
                this.$http.get(api).then((response) => {
                    console.log(response.data)
                    vm.isLoading = false
                    vm.coupons = response.data.coupons
                    vm.pagination = response.data.pagination
                    this.$bus.$emit('page:push',response.data.pagination)
                })
            },
            openModal(isNew,item) {
                this.due_date = ""
                $('#couponModal').modal('show')
                if(isNew){
                    this.tempCoupon = {}
                    this.isNew = true
                }
                else{
                    this.tempCoupon = Object.assign({},item)
                    this.isNew = false
                    let date = new Date(this.tempCoupon.due_date * 1000).toISOString().split('T');
                    this.due_date = date[0]
                }
            },
            updateCoupon() {
                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`
                let httpMethod = 'post'
                const vm = this
                let toPage = 1
                if(!this.isNew){
                    api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.tempCoupon.id}`
                    httpMethod = 'put'
                    toPage = this.pagination.current_page
                }
                this.$http[httpMethod](api,{data:vm.tempCoupon}).then((response) => {
                    console.log(response.data)
                    if(response.data.success){
                        $('#couponModal').modal('hide')
                        vm.getCoupons(toPage)
                    }
                    else{
                        $('#couponModal').modal('hide')
                        vm.getCoupons(toPage)
                        console.log('新增失敗')
                    }
                })
            },
            openDelCouponModal(item) {
                $('#delCouponModal').modal('show')
                this.tempProduct = Object.assign({},item)
            },
            deleteCoupon() {
                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.tempProduct.id}`
                const vm = this
                this.$http.delete(api).then((response) => {
                    if(response.data.success){
                        $('#delCouponModal').modal('hide')
                        vm.getCoupons()
                    }
                    else{
                        $('#delCouponModal').modal('hide')
                        vm.getCoupons()
                        console.log('刪除失敗')
                    }
                })
            },
        },
        watch:{
            due_date() {
                const vm = this
                const timestamp = Math.floor(new Date(vm.due_date) / 1000)
                this.tempCoupon.due_date = timestamp
            }
        },
        created() {
            // const vm = this;
            this.getCoupons();
            // this.$bus.$on('page:change',(page) =>{
            //     vm.getCoupons(page);
            // })
        },
        components:{
            Pagination
        }
    }
</script>


