<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" v-if="item.is_enabled" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                        :style="{backgroundImage:`url(${item.image})`}">
                    </div>
                    <div class="card-body">
                        <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                        <h5 class="card-title">
                            <a href="#" class="text-dark">{{ item.title }}</a>
                        </h5>
                        <p class="card-text">{{ item.content }}</p>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h5" v-if="!item.price">原價 {{ item.origin_price }} 元</div>
                            <del class="h6" v-if="item.price">原價 {{ item.origin_price }} 元</del>
                            <div class="h5" v-if="item.price">現在只要 {{ item.price }} 元</div>
                        </div>
                    </div>
                    <div class="card-footer d-flex">
                        <button type="button" @click="getProduct(item.id)" class="btn btn-outline-secondary btn-sm">
                            <i v-if="status.loadingItem==item.id" class="fas fa-spinner fa-spin"></i>
                            查看更多
                        </button>
                        <button type="button" @click="addToCart(item.id)" class="btn btn-outline-danger btn-sm ml-auto">
                            <i v-if="status.loadingItem==item.id" class="fas fa-spinner fa-spin"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="cart.carts.length>0" class="row justify-content-center my-5">
            <div class="row justify-content-center my-5">
                <table class="table table-hover">
                    <thead>
                        <tr>
                            <th></th>
                            <th>品名</th>
                            <th>數量</th>
                            <th>單價</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,key) in cart.carts" :key="key">
                            <td class="align-middle">
                                <button class="btn btn-outline-danger btn-sm" @click="deleteCartItem(item.id)">
                                    <i class="fas fa-trash-alt"></i>
                                </button>
                            </td>
                            <td class="align-middle">
                                {{ item.product.title }}
                                <div v-if="item.coupon" class="text-success">
                                    已套用優惠碼
                                </div>
                            </td>
                            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                            <td class="align-middle text-right">{{ item.total | currency }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="3" class="text-right">
                                總計
                            </td>
                            <td class="text-right">
                                {{ cart.total | currency }}
                            </td>
                        </tr>
                        <tr v-if="cart.total!=cart.final_total">
                            <td colspan="3" class="text-right">
                                折扣價
                            </td>
                            <td class="text-right">
                                {{ cart.final_total | currency }}
                            </td>
                        </tr>
                    </tfoot>
                </table>
                <div class="input-group mb-3 input-group-sm">
                    <input v-model="couponCode" type="text" class="form-control" placeholder="請輸入優惠碼">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" @click="useCouponCode">套用優惠碼</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-validate="'required|email'"
                        :class="{'is-invalid':errors.has('email')}"
                        v-model="form.user.email" placeholder="請輸入 Email">
                    <span class="text-danger">{{ errors.first('email') }}</span>
                </div>
            
                <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" name="name" id="username"
                        v-validate="'required'"
                        :class="{'is-invalid':errors.has('name')}"
                        v-model="form.user.name" placeholder="輸入姓名">
                    <span v-if="errors.has('name')" class="text-danger">請輸入姓名</span>
                </div>
            
                <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input type="tel" class="form-control" id="usertel" name="tel"
                        v-validate="'required'"
                        :class="{'is-invalid':errors.has('tel')}"
                        v-model="form.user.tel" placeholder="請輸入電話">
                    <span v-if="errors.has('tel')" class="text-danger">請輸入電話</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                        v-validate="'required'"
                        :class="{'is-invalid':errors.has('address')}"
                        placeholder="請輸入地址">
                    <span v-if="errors.has('address')" class="text-danger">地址欄位不得留空</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">留言</label>
                    <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                    <div class="text-right">
                    <button class="btn btn-danger">送出訂單</button>
                </div>
            </form>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">
                            {{ product.title }}
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <img :src="product.image" alt="" class="img-fluid">
                        <blockquote class="blockquote mt-4">
                            <p class="mb-0">{{ product.content }}</p>
                            <footer class="blockqoute-footer text-right">{{ product.description }}</footer>
                        </blockquote>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h4" v-if="!product.price">原價 {{ product.origin_price }} 元</div>
                            <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
                            <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
                        </div>
                        <select name="" class="form-control mt-3" v-model="product.num">
                            <option :value="num" v-for="num in 10" :key="num">
                                選購 {{num}} {{product.unit}}
                            </option>
                        </select>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            小計 <strong>{{ product.num * product.price }}</strong> 元
                        </div>
                        <button type="button" @click="addToCart(product.id,product.num)" class="btn btn-primary" >
                            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
    export default{
        data() {
            return{
                products:[],
                product:{},
                pagination:{},
                status:{
                    loadingItem:""
                },
                cart:{
                    carts:[],
                    final_total:0,
                    total:0,
                    couponCode:""
                },
                isLoading:false,
                couponCode:"",
                form:{
                    "user": {
                    "name": "",
                    "email": "",
                    "tel": "",
                    "address": ""
                    },
                    "message": ""
                }
            }
        },
        methods:{
            getProducts(page = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`
                const vm = this
                vm.isLoading = true
                this.$http.get(api).then((response) => {
                    console.log(response.data)
                    vm.isLoading = false
                    vm.products = response.data.products
                    vm.pagination = response.data.pagination
                })
            },
            getProduct(id) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
                const vm = this
                vm.status.loadingItem = id
                this.$http.get(api).then((response) => {
                    vm.product = response.data.product
                    if(vm.product.num){
                        vm.product.num = 1
                    }
                    else{
                        vm.$set(vm.product,"num",1)
                    }
                    $('#productModal').modal('show')
                    console.log(response.data)
                    vm.status.loadingItem = ''
                })
            },
            addToCart(id,qty = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
                const vm = this
                vm.status.loadingItem = id
                vm.isLoading = true
                const cart = {
                    product_id:id,
                    qty
                }
                this.$http.post(api,{data:cart}).then((response) => {
                    console.log(response.data)
                    vm.status.loadingItem = ''
                    vm.getCart()
                    vm.isLoading = false
                    $('#productModal').modal('hide')
                })
            },
            getCart() {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
                const vm = this
                vm.isLoading = true
                this.$http.get(api).then((response) => {
                    console.log(response.data)
                    vm.cart = response.data.data
                    vm.isLoading = false
                })
            },
            deleteCartItem(id) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
                const vm = this
                vm.isLoading = true
                this.$http.delete(api).then(() => {
                    vm.getCart()
                    vm.isLoading = false
                })
            },
            useCouponCode() {
                if(this.couponCode){
                    const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
                    const vm = this
                    vm.isLoading = true
                    this.$http.post(api,{data:{code:vm.couponCode}}).then((response) => {
                        vm.getCart()
                        vm.isLoading = false
                    })
                }
            },
            createOrder() {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`
                const vm = this
                const order = this.form
                this.$validator.validate().then(result => {
                    if (result) {
                        this.$http.post(api,{data:order}).then((response) => {
                            if(response.data.success){
                                console.log('訂單建立成功',response)
                                vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                            }
                            
                        })
                    }
                    else{
                        console.log('欄位不完整')
                    }
                });
                
            }
        },
        created() {
            this.getProducts();
            this.getCart();
        }
    }
</script>


