<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th @click="daySortChange">購買時間
                        <i v-if="!daySort" class="fas fa-sort"></i>
                        <i v-if="daySort==1" class="fas fa-sort-down"></i>
                        <i v-if="daySort==2" class="fas fa-sort-up"></i>
                    </th>
                    <th>Email</th>
                    <th>購買款項</th>
                    <th>應付金額</th>
                    <th @click="paidSortChange">是否付款 
                        <i v-if="!paidSort" class="fas fa-sort"></i>
                        <i v-if="paidSort==1" class="fas fa-sort-down"></i>
                        <i v-if="paidSort==2" class="fas fa-sort-up"></i>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in orderSort" :key="item.id">
                    <td>{{ item.create_at | date }}</td>
                    <td><span v-if="item.user">{{ item.user.email }}</span><span v-else>無</span></td>
                    <td>
                        <ul class="list-unstyled">
                            <li v-for="productItem in item.products" :key="productItem.id">
                                {{ productItem.product.title }} 數量 : {{ productItem.qty }} {{ productItem.product.unit }}
                            </li>
                        </ul>
                    </td>
                    <td width="120px" class="text-right">{{ item.total | currency }}</td>
                    <td>
                        <span v-if="item.is_paid" class="text-success">已付款</span>
                        <span v-else>尚未付款</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <Pagination :pagination="pagination" @pageChange="getOrders"></Pagination>
    </div>
</template>

<script>
import Pagination from './Pagination'
    export default{
        data() {
            return{
                orders:[],
                isLoading:false,
                pagination:{},
                paidSort:"",
                daySort:""
            }
        },
        methods:{
            getOrders(page = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`
                const vm = this
                vm.isLoading = true
                this.$http.get(api).then((response) => {
                    console.log(response.data)
                    vm.isLoading = false
                    vm.orders = response.data.orders
                    vm.pagination = response.data.pagination
                    // this.$bus.$emit('page:push',response.data.pagination)
                })
            },
            paidSortChange() {
                if(!this.paidSort){
                    this.paidSort = 1
                }
                else if(this.paidSort==1){
                    this.paidSort = 2
                }
                else{
                    this.paidSort = 1
                }
                this.daySort = ""
            },
            daySortChange() {
                if(!this.daySort){
                    this.daySort = 1
                }
                else if(this.daySort==1){
                    this.daySort = 2
                }
                else{
                    this.daySort = 1
                }
                this.paidSort = ""
            }
        },
        computed:{
            orderSort() {
                var newOrders = [...this.orders]
                var sortedOrders = []
                if(this.paidSort){
                    if(this.paidSort==1){
                        sortedOrders = newOrders.sort(function(a,b){
                            if(a.is_paid>b.is_paid){
                                return -1
                            }
                            else if(a.is_paid<b.is_paid){
                                return 1
                            }
                            else{
                                return -1
                            }
                        })
                    }
                    else if(this.paidSort==2){
                        sortedOrders = newOrders.sort(function(a,b){
                            if(a.is_paid>b.is_paid){
                                return 1
                            }
                            else if(a.is_paid<b.is_paid){
                                return -1
                            }
                            else{
                                return -1
                            }
                        })
                    }
                }
                else if(this.daySort){
                    if(this.daySort==1){
                        sortedOrders = newOrders.sort(function(a,b){
                            if(a.create_at>b.create_at){
                                return -1
                            }
                            else{
                                return 1
                            }
                        })
                    }
                    else if(this.daySort==2){
                        sortedOrders = newOrders.sort(function(a,b){
                            if(a.create_at>b.create_at){
                                return 1
                            }
                            else{
                                return -1
                            }
                        })
                    }
                }
                else{
                    sortedOrders = [...newOrders]
                }
                return sortedOrders
            }
        },
        created() {
            // const vm = this;
            this.getOrders();
            // this.$bus.$on('page:change',(page) =>{
            //     vm.getOrders(page);
            // })
        },
        components:{
            Pagination
        }
    }
</script>


